<template>
    <div>
        <custom-dialog :title="title" :content="textContent" :button1-text="cancelButtonText" :button2-text="confirmButtonText" :open.sync="open" @button1="cancel_clicked" @button2="confirm_clicked">
          <template v-slot:content><slot name="content"></slot></template>
        </custom-dialog>
    </div>
</template>
<script>
import CustomDialog from '@/common/BaseComponents/CustomDialog/CustomDialog.vue'
export default {
    components: {
        CustomDialog
    },

    props: {
        title: { type: String, default: 'Default Modal Title' },
        textContent: { type: String, default: 'Default Modal Text Content' },
        cancelButtonText: { type: String, default: 'Cancel' },
        confirmButtonText: { type: String, default: 'Confirm' },
        openDialog: Boolean
    },

    data: () => ({
        open: false
    }),

    watch: {
        openDialog(val) {
            this.open = val
        },
        open(val) {
            this.$emit('update:openDialog', val)
        }
    },

    methods: {
        cancel_clicked() {
            this.open = false
        },
        confirm_clicked() {
            this.$emit('confirm')
        },
        showDialog() {
            this.open = true
        },
        closeDialog() {
            this.open = false
        }
    }
}
</script>